import React, { useState, useEffect } from 'react';
import axios from 'axios';

const apiUrl = `https://api.nytimes.com/svc/books/v3/lists/current/hardcover-fiction.json?api-key=x32SgZMGtyG07rEkpc2jCZQHASiFgPah`;

const Books = () => {
  const [books, setBooks] = useState([]);

  useEffect(() => {
    const fetchBooks = async () => {
      const response = await axios.get(apiUrl);
      setBooks(response.data.results.books);
      console.log(response.data.results);
    };
    fetchBooks();
  }, []);
  return (
    <div>
      <h1>New York Times Best Selling Books</h1>
      <section>
        {books.map((book) => {
          const {
            book_image,
            title,
            author,
            rank,
            description,
            amazon_product_url,
            publisher,
          } = book;

          return (
            <div className='grid gap-4'>
              <div className='top'>
                <h2 className='rank'>{rank}</h2>
                <img className='image' src={book_image} alt={title}></img>
                <h2 className='title'>{title}</h2>
                <h3 className='author'>Author: {author}</h3>
              </div>
              <div className='details'>
                <p className='desc'>{description}</p>
                <p classname='publisher'>Publisher: {publisher}</p>
                <button className='bg-blue-700'>
                  <a href={amazon_product_url}></a>BUY
                </button>
              </div>
            </div>
          );
        })}
      </section>
    </div>
  );
};

export default Books;
